let count = 0;

export const runTest = () => {
  let failed = false;
  try {
    history.pushState(null, '', 'test-me' + count);
    count++;
  } catch (ex) {
    document.querySelector('#console').innerHTML += `
    <code><pre>PushState Exception: ${ex.message}\n${ex.stack}</pre></code>`;
    failed = true;
  }

  try {
    console.log('hello');
    console.log('hello2');
  } catch (ex) {
    document.querySelector('#console').innerHTML += `
    <code><pre>ConsoleLog Exception: ${ex.message}\n${ex.stack}</pre></code>`;
    failed = true;
  }

  if (!failed) {
    document.querySelector('#console').innerHTML += `
    <code><pre>Test Ran Successfully</pre></code>`;
  }
};



